import PropTypes from "prop-types";
import cx from "classnames"

import styleModule from "./Section.module.css"


const Section = ({children, id=null, cssClass=""}) => (
    <div id={id} className={`${styleModule.section} ${cssClass}`}>
        <div className={styleModule.section__content}>{children}</div>
    </div>
);

Section.propTypes = {
    children: PropTypes.array.isRequired,
    cssClass: PropTypes.string
}


export default Section;